#search{
    margin:-12vh 5% 25px 5%;
    font-size: medium;
    border-style: none;
    box-shadow: 1px 1px 2px 0px hsl(180, 8%, 52%);
    border-radius: 5px;
    padding: 20px;
    position: sticky;
    top:1%;
    background-color: white;
    display: flex;
    flex-wrap: wrap;
    padding-right: 50px;
    gap: 10px;
    z-index: 1;
    transition: 0.8s;
    .button{
       position: absolute;
        right: 2%;
        top: 50%; 
        transform: translate(0%, -50%);
        display: flex;
        gap: 10px;
        @media(max-width:700px) {
            display: grid;
        }
    }
    
    
    #location{
        border-left: #7b8e8e 1px solid;
        @media (max-width:700px){
            border-style: none;
        }
    }
}
.clear{
    background: none;
    border-style: none;
    outline: none;
    
    cursor: pointer;
    color: #7b8e8e;
    font-weight: 700;
    font-size: small;
    &:hover{
       color: #5ba4a4; 
       text-decoration: underline;
    }
}

input[type=search]:focus{
    outline: none;
}

form{
    display: grid;
    grid-template-columns: 60% 20% 10%;
    width: 100%;
    @media(max-width:700px) {
    grid-template-columns: 100%;
    gap: 20px;
    }
    input[type=search]{
        border-style: none;
        /* clears the 'X' from Internet Explorer */
        &::-ms-clear {
            display: none; width : 0; height: 0; 
        }
        &::-ms-reveal {  
            display: none; width : 0; height: 0; 
        }

        /* clears the 'X' from Chrome */
        &::-webkit-search-decoration,
        &::-webkit-search-cancel-button,
        &::-webkit-search-results-button,
        &::-webkit-search-results-decoration {
             display: none; 
        }
    }
}